



















import Vue from 'vue';
import PaperPlane from '../icons/PaperPlane.vue';
import Check from '../icons/Check.vue';
export default Vue.extend({
	data() {
		const sendId = `send-input-${Date.now()}`;
		return {
			sendId,
			shiftPressed: false,
			hasValue: false,
			name: '',
		};
	},
	props: {
		setupName: {
			type: Boolean,
			default: false,
		},
	},
	components: {
		PaperPlane,
		Check,
	},
	name: 'MessageInput',
	methods: {
		onKeyDown(e: KeyboardEvent) {
			if (e.key === 'Shift') {
				this.shiftPressed = true;
			} else if (e.key === 'Enter' && !this.shiftPressed) {
				e.preventDefault();
				e.stopPropagation();
				this.send();
			}
		},
		onKeyUp(e: KeyboardEvent) {
			if (e.key === 'Shift') {
				this.shiftPressed = false;
			}
		},
		getValue(): string {
			let value = '';
			if (this.setupName) {
				return this.name;
			} else {
				const sendInput = document.getElementById(this.sendId);
				if (sendInput) {
					value = sendInput.innerText;
					sendInput.innerText = '';
				}
			}
			return value;
		},
		send() {
			const text = this.getValue();
			if (text && !/^\s*$/.test(text)) {
				if (this.setupName) this.$emit('saveName', text);
				else this.$emit('send', text);
			}
		},
	},
});
